@import '../_variables.sass';

.mobile-cat {
	&__wrapper {
		padding-top: 20px;
		background-color: #fff;
		display: flex;
		justify-content: center; }
	&__category {
		position: relative;
		display: inline-block;
		width: calc(100% - 40px);
		.mobile-cat__content {
			box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.2);
			width: 300px; } }
	&__category-toggle {
		font-weight: 600;
		border-radius: 5px;
		border: none;
		outline: none;
		padding: 0 30px;
		width: 100%;
		text-transform: uppercase;
		justify-content: space-between;
		position: relative;
		font-size: 22px;
		height: 80px;
		display: flex;
		align-items: center;
		background-color: rgb(0,122,255);
		color: #ffffff;
		&:hover {
			cursor: pointer;
			background-color: rgba(0,122,255, 0.8); }
		svg {
			font-size: 20px;
			transform: translateY(-5px); } }
	&__content {
		position: absolute;
		z-index: 10;
		.mobile-cat__item {
			border-bottom: 1px solid #d9d9d9;
			width: calc(100vw - 60px);
			font-size: 18px;
			background-color: #fff;
			color: black;
			padding: 12px 16px;
			text-decoration: none;
			display: block;
			&:last-child {
				border-bottom: none; }
			&:hover {
				cursor: pointer;
				background-color: #f5f5f5; } } }
	&--small {
		.mobile-cat__toggle--small {
			background-color: #fff;
			width: 150px;
			height: 72px;
			text-transform: uppercase;
			font-weight: 600;
			font-size: 16px;
			border: none;
			outline: none;
			&#deals {
				color: #ff3b30; }
			&:hover {
				cursor: pointer;
				color: #007aff; }
			svg {
				transform: translate(5px, -2px); } }
		.mobile-cat__content {
			box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.2); }

		.mobile-cat__item {
			width: 180px;
			font-size: 16px; } } }


