	&__column {
		height: 100%;
		align-content: center;
		&--left {
			display: flex;
			flex-direction: column;
			align-items: space-around;
			width: 20%;
			padding: 20px 20px 0 20px;
			justify-content: center;
			p {
				font-size: 26px;
				max-width: 150px; }
			p.bold {
				font-weight: 600; }
			a.bestselling__link {
				font-size: 16px;
				color: #007aff;
				text-transform: uppercase;
				font-weight: 600; } }
		&--right {
			width: 80%; } }
