.wrapper {
	.top-container {
		background-color: #fff;
		display: flex;
		width: 100%;
		height: 110px;
		align-items: center;
		justify-content: space-between;
		&__column {
			display: flex;
			width: 25%;
			height: 100%;
			align-items: center;
			padding: 0 0 0 50px;
			&--left {
				min-width: 300px; }
			&--right {
				justify-content: space-between;
				padding: 0 50px 0 20px;
				min-width: 370px; } }
		&__search-column {
			display: flex;
			width: auto; }
		p {
			font-size: 26px; }
		&__logo {
			&:hover {
				cursor: pointer; } }
		.icon {
			height: 50px;
			width: 50px;
			text-align: center;
			line-height: 50px;
			font-size: 32px;
			color: #303030;
			&:hover {
				color: #cdcdcd;
				cursor: {} } } }

	.icon__number {
		user-select: none;
		font-weight: 600;
		font-size: 16px; } }


.wrapper--medium {
	.top-container {
		&__column {
			display: flex;
			width: 25%;
			height: 100%;
			align-items: center;
			padding: 0 0 0 2%; } } }




.wrapper.wrapper--mobile {
	.top-container {
		background-color: #007aff;
		display: flex;
		width: 100%;
		height: 140px;
		align-items: center;
		justify-content: space-between;
		&__column {
			display: flex;
			width: 35%;
			height: 100%;
			align-items: center;
			padding: 0 0 0 10px;
			&--right {
				width: 55%;
				justify-content: flex-end;
				padding: 0 10px; } }
		&__search-column {
			display: flex;
			width: 10%; }
		.icon {
			height: 50px;
			width: 50px;
			margin-right: 40px;
			text-align: center;
			line-height: 50px;
			font-size: 40px;
			color: #fff;
			&:hover {
				color: #cdcdcd;
				cursor: pointer; } }

		.menu-toggle {
			width: 56px;
			height: 56px;
			border-radius: 50%;
			font-size: 48px;
			color: #fff;
			&:hover {
				color: #cdcdcd;
				cursor: pointer; }
			&:active {
				font-size: 46px; }
			&--yellow {
				background-color: #fde401;
				color: #111;
				&:hover {
					color: #777; } } } } }
