@import '../variables.sass';
.cart {
	padding: 20px 70px;
	display: flex;
	flex-direction: column;
	width: 100%;
	h2 {
		margin-bottom: 20px; }
	&__items {
		display: flex;
		width: 100%;
		justify-content: flex-start;
		align-items: center;
		flex-direction: column; }
	.no-cart {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 400px;
		font-size: 200px;
		color: #f7f7f7;
		align-items: center;
		span {
			align-self: flex-start;
			font-size: 1.2rem;
			color: #000;
			margin-bottom: 100px; } } }


