/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before {
    display: table;
    content: ''; }
  .slick-track:after {
    display: table;
    content: '';
    clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px; }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-slide img {
  display: block; }

.slick-slide.slick-loading img {
  display: none; }

.slick-slide.dragging img {
  pointer-events: none; }

.slick-initialized .slick-slide {
  display: block; }

.slick-loading .slick-slide {
  visibility: hidden; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

section.bestselling-products {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  padding: 0 70px 20px 70px;
  margin-bottom: 20px; }

.bestselling-products__row {
  height: 80px; }
  .bestselling-products__row--top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px 20px 0 20px;
    border-bottom: 2px solid #eee; }
    .bestselling-products__row--top p {
      font-size: 26px; }
    .bestselling-products__row--top p.bold {
      font-weight: 600; }
  .bestselling-products__row--bottom {
    padding-top: 10px;
    width: 100%;
    min-height: 350px; }

.bestselling-products__buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #7a7a7a;
  font-size: 32px;
  outline: none;
  border: none;
  margin-right: 8px;
  background-color: #fff; }
  .bestselling-products__buttons:hover {
    cursor: pointer; }
  .bestselling-products__buttons .bestselling-products__button--right {
    margin-right: 0px; }

@media screen and (max-width: 1000px) {
  section.bestselling-products {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 0 20px 4px; } }
